import React, { Component } from 'react';
import logo from './Icon.png';
import './App.css';
import stores from './stores.json';
import { Button, ButtonToolbar} from 'react-bootstrap';
import Customers from './Customers';
import { BrowserRouter as Router, Switch, Route, Redirect} from 'react-router-dom';
import { Map, Marker, GoogleApiWrapper, InfoWindow, Polygon } from 'google-maps-react';

// import CurrentLocation from './Map';

const mapStyles = {
  width: '100%',
  height: '100%',
};

// draw up a boundary (didn't see the boundary being drawn up)
const triangleCoords=[
  {lat: 1.306995, lng: 103.849370},
  {lat: 1.313306, lng: 103.854177},
  {lat: 1.309308, lng: 103.857776},
  {lat: 1.303510, lng: 103.854064},
  {lat: 1.306995, lng: 103.849370}
];

class App extends Component {
  constructor(props){
    super(props);

    this.state={
      showingInfoWindow:false,
      activeMarker:{},
      selectedPlace:{},
      currentLocation: {
        lat: 1.308695,
        lng: 103.853076
      },
      loading: true
    }
  }

  // get current location
  componentDidMount(props) {
    navigator.geolocation.getCurrentPosition(
      position => {
        const { latitude, longitude } = position.coords;

        this.setState({
          currentLocation: { lat: latitude, lng: longitude },
          loading: false
        });
      },
      () => {
        this.setState({ loading: false });
      }
    );
  }
  
  // show infowindow when the marker is clicked
  onMarkerClick = (props, marker, e) =>
    this.setState({
      selectedPlace: props,
      activeMarker: marker,
      showingInfoWindow: true,
    });

  // close infowindow when the user clicks on the close button 
  onClose = props => {
    if (this.state.showingInfoWindow) {
      this.setState({
        showingInfoWindow: false,
        activeMarker: null
      });
    }
  };

  // display markers
  displayMarkers= (poi) =>{
    return poi.map((poi,index) => {
      return <Marker Key={index} id={index} position={{
        lat: poi.latitude,
        lng: poi.longitude,
      }}
      onClick={this.onMarkerClick}
      name={poi.name}/>
    })
  }  

  render() {
    console.log("Host URL"+process.env.PUBLIC_URL);
    const { loading, currentLocation } = this.state;

    if (loading) {
      return null;
    }

    return (

      <Router basename={process.env.PUBLIC_URL}>
        <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h1 className="App-title">Welcome to Little India!</h1>
        </header>
        
        <div id='map'>

          <Map
          google={this.props.google}
          zoom={15.5}
          style={mapStyles}
          // initialCenter={currentLocation}
          intialCenter={{
            lat: 1.308695, lng: 103.853076
           }}
          > 

          <Polygon
                path={triangleCoords}
                strokeColor="#0000FF"
                strokeOpacity={0.8}
                strokeWeight={2}
                fillColor="#0000FF"
                fillOpacity={0.35}
                name={"Little India Precinct"}
              />


          {this.displayMarkers(stores)}

          <Marker position={currentLocation} onClick={this.onMarkerClick} name={'current location'}/>

          <InfoWindow
            marker={this.state.activeMarker}
            visible={this.state.showingInfoWindow}
            onClose={this.onClose}
          >
            <div>
              <h4>{this.state.selectedPlace.name}</h4>
            </div>
          </InfoWindow>
          </Map>
        </div>

          <Switch>
                <Route exact path= "/" render={() => (
                  <Redirect to="/customerlist"/>
                )}/>
                 <Route exact path='/customerlist' component={Customers} />
          </Switch>
      </div>
    </Router>
    );
  }
}

// export default App;
export default GoogleApiWrapper({
  apiKey: ('AIzaSyDj4msBkNl1a8j6I0wUU37FWX7C-OexnxI')
})(App);

