import React, {Component} from 'react';
import Panel from 'react-bootstrap/lib/Panel'
import axios from 'axios'
import arrow from './sampleimages/dropdown_arrow.jpg'

//This Component is a child Component of Customers Component
export default class CustomerDetails extends Component {

  constructor(props) {
    super(props);
    this.state = {
      listOpen:0,
      rotate:false,
      visible: false,
    };
    this.toggleList = this.toggleList.bind(this);
  }

  // Toggle the dropdown menu
  toggleList(index){
    this.setState(prevState => ({
      visible: !this.state.visible,
      listOpen: index,
      rotate: !prevState.rotate
    }))
  }

  //Function which is called when the component loads for the first time
  componentDidMount() {
    this.getCustomerDetails(this.props.val)
  }

  //Function which is called whenver the component is updated
  componentDidUpdate(prevProps) {

    //get Customer Details only if props has changed
    if (this.props.val !== prevProps.val) {
      this.getCustomerDetails(this.props.val)
    }
  }

  //Function to Load the customerdetails data from json.
  getCustomerDetails(id) {
    axios.get('assets/samplejson/customer' + id + '.json').then(response => {
      this.setState({customerDetails: response})
    })
  };

  render() {
    const{listOpen,visible,rotate} = this.state

    if (!this.state.customerDetails)
      return (<p>Loading Data</p>)
    return (<div className="customerdetails">
      <div className="addmargin">
      <div className="col-md-9">
      {this.state.customerDetails.data.map((customer,index) => (
        <Panel bsStyle="info" key={customer.name}>
          <img src={require(`./sampleimages/${customer.image}.jpg`)} className="Customer-image"/>
          <br line-height="110%"></br>
          <Panel.Heading>
            <Panel.Title componentClass="h3" align="left"><strong>{customer.name}</strong></Panel.Title>
          </Panel.Heading>

          <Panel.Body>
            <p align="left">{customer.desc}</p>

            {/* Toggle dropdown menu in a wrapper*/}
            <div className="dd-wrapper">
              <button className="buttony"><img src={arrow} alt="image"
              className={rotate? "rotate":""} onClick={() => {this.toggleList(index)}}/></button>
              
              {listOpen === index && visible && <ol className="dd-list">
                <br line-height="110%"></br>
                <p align="left"><strong>Quick tips on {customer.about}</strong></p>
                <li align="left" class="dropdown" className="dd-list-item" key={customer.name}>{customer.tip1}</li>
                <br line-height="75%"></br>
                <li align="left" class="dropdown" className="dd-list-item" key={customer.name}>{customer.tip2}</li>
                <br line-height="75%"></br>
                <li align="left" class="dropdown" className="dd-list-item" key={customer.name}>{customer.tip3}</li>
              </ol>}
            </div>

          </Panel.Body>
      </Panel>
      ))} 
    </div>
    </div>
    </div>)
  }
}


